<div class="c-quick__device"><h2>{{ title }}</h2><router-link class="btn-more" to="/Devices"><mdicon name="dots-horizontal" size="15"></mdicon></router-link><el-scrollbar class="pst--scroll" height="155px"><div class="c-card"><div class="c-card__item" v-for="(item, index) of device"><div class="icon"><mdicon :name="item.icon" size="20"></mdicon></div><dl><dt>{{ item.name }}</dt><dd>{{ item. value }}</dd></dl><button class="btn-switch" :class="{'is-active' : item.state === 'on'}" :title="item.state" @click="power(item)"></button></div></div></el-scrollbar></div>